.home {

  .homeContent {
    background: linear-gradient(115deg, $color-1 50%, transparent 100%);
    min-height: $minHeight;
    width: 100%;
    min-height: 500px;
    border-radius: $borderRadius;
    position: relative;
    overflow: hidden;

    &:after {
      content: '';
      position: absolute;
      height: 100%;
      width: inherit;
      background: url('../../media/moiplage2.jpeg') no-repeat center/cover;
      z-index: -1;
      filter: brightness(50%);
    }

    .content {
      position: absolute;
      top: 20%;
      left: 13%;
      font-family: $font-1;

      h1 {
        font-size: 5rem;
        font-family: $font-3;
        color: $color-5;
        text-transform: uppercase;
        text-shadow: 3px 6px 2px $black;
      }
      h2 {
        font-size: 2.5rem;
        padding: 1.5rem 0 7rem;
        color: $color-3;
        text-shadow: 1px 4px 1px $black;
      }
      h3{
        color:$color-5;
        margin-top: -100px;
      }
      p{
        color: $color-3;
        margin-bottom: 50px;
        margin-top:50px;
      }
      .pdf {
        a {
          font-size: 1.5rem;
          background: $color-3;
          padding: 1rem 2rem;
          border-radius: $borderRadius;
          box-shadow: 0 5px 1px rgb(15, 15, 15);
          transition: 0.2s ease;

          &:hover {
            background: $color-4;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 850px){
  .homeContent {
    margin: 0 !important;
  }
  h1 {
    font-size: 1.5rem !important;
    margin-top: -25px;
  }
  h2 {
    font-size: 1rem !important;
  }h3{
     font-size: 1rem !important; ;
   }

  p
  {
    font-size: 0.7rem !important;
    margin-top: 10px !important;
  }
  a{
    font-size: 1rem !important;

  }
}
@media screen and (max-width:580px){
  .homeContent {
    &:after {
      background: url('../../media/moiplage2.jpeg') no-repeat 60% 50%/cover !important;
    }
    h1 {
      font-size: 1.5rem !important;
      margin-top: -50px;
    }
    h2 {
      font-size: 1rem !important;
    }h3{
    font-size: 1rem !important; ;
         }

    p
         {
    font-size: 0.7rem !important;
      margin-top: 10px !important;
         }
    a{
      font-size: 1rem !important;
    }
  }
}